import { useState } from 'react';
import { useDispatch } from 'react-redux';
import styled from 'styled-components';
import { string } from 'prop-types';
import {
  StyledTitle,
  StyledContent,
  StyledInput,
  StyledButton,
} from './auth.styles';
import { login } from '../../../modules/auth/user';
import Verified from '../../../assets/common/drawer_email-check.svg';

const LoginTitle = styled(StyledTitle)``;
const LoginContent = styled(StyledContent)`
  & > .loginForm {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
`;
const LoginButton = styled(StyledButton)`
  margin-top: 33px;
`;
const LoginInput = styled(StyledInput)``;
const Login = ({ email }) => {
  const dispatch = useDispatch();
  const [password, setPassword] = useState('');

  const handleLogin = e => {
    e.preventDefault();
    dispatch(login(email, password));
  };
  const handlePasswordInput = e => {
    setPassword(e.target.value);
  };

  let disabled = true;
  if (password) {
    disabled = false;
  }
  return (
    <>
      <LoginTitle>
        <p>
          <span className="stress">  </span>
        </p>
        <p>.</p>
      </LoginTitle>
      <LoginContent>
        <form className="loginForm" onSubmit={handleLogin}>
          <LoginInput>
            <label htmlFor="email"></label>
            <input readOnly type="email" id="email" value={email} />
            <img className="verified" src={Verified} alt="verified" />
          </LoginInput>
          <LoginInput>
            <label htmlFor="password"></label>
            <input
              type="password"
              id="password"
              placeholder=" "
              value={password}
              onInput={handlePasswordInput}
            />
          </LoginInput>
          <LoginButton disabled={disabled} type="submit">
            
          </LoginButton>
        </form>
      </LoginContent>
    </>
  );
};

Login.propTypes = {
  email: string.isRequired,
};
export default Login;
